body, html {
  margin: 0;
  padding: 0;
  font-family: Spectral, system-ui;
}

header {
  color: #fff;
  text-align: center;
  padding: 10px 20px;
  position: relative;
}

.date h1, .day h1 {
  color: #000;
  text-align: right;
  margin-left: 20px;
  font-family: Anonymous Pro, monospace;
  font-size: 1rem;
  position: relative;
}

header img {
  width: 35%;
  height: auto;
  margin-top: 0;
}

nav {
  margin-bottom: 0;
}

nav ul {
  padding: 0;
  list-style-type: none;
}

nav ul li {
  margin-right: 20px;
  display: inline;
}

a {
  color: #000;
  text-decoration: none;
}

.main-content {
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

.content-wrapper {
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;
  display: grid;
}

.main-article {
  text-align: center;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.main-article img {
  width: 800px;
  max-width: 300px;
  height: auto;
  margin-bottom: 15px;
}

.main-article h2 {
  margin-bottom: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.main-article p {
  margin-bottom: 10px;
  font-size: 1rem;
}

.main-article a {
  color: #00f;
  margin-top: 10px;
  font-size: 1rem;
  text-decoration: underline;
}

.category {
  text-align: left;
  color: #666;
  margin-bottom: 5px;
  font-size: .9rem;
}

.other-articles {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.other-articles .article {
  border: 1px solid #ccc;
  align-items: center;
  padding: 10px;
  display: flex;
}

.other-articles img {
  width: 200px;
  height: auto;
  margin-right: 10px;
}

.other-articles h2 {
  text-align: center;
  margin: 0;
  font-size: 10px;
  line-height: 1.5rem;
}

.other-articles p {
  text-align: center;
  margin: 0;
  font-size: 1rem;
  line-height: 1.5rem;
}

.more-articles a {
  color: #00f;
  margin-bottom: 10px;
  display: block;
}

.suggestions-list {
  background-color: #fff;
  border: 1px solid #ccc;
  width: 100%;
  max-height: 150px;
  position: absolute;
  overflow-y: auto;
}

.suggestion-item {
  cursor: pointer;
  padding: 5px;
}

.suggestion-item:hover {
  background-color: #000;
}

@media (width <= 768px) {
  .content-wrapper {
    grid-template-columns: 1fr;
  }

  .main-content {
    flex-direction: column;
  }
}
