body, html {
    margin: 0;
    padding: 0;
    font-family: "Spectral", system-ui;
}

header {
    color: white;
    padding: 10px 20px;
    text-align: center;
    position: relative;
}

.date h1, .day h1 {
    color: rgb(0, 0, 0);
    font-family: "Anonymous Pro", monospace;
    position: relative;
    margin-left: 20px;
    font-size: 1rem;
    text-align: right;
}

header img {
    width: 35%;
    height: auto;
    margin-top: 0;
}

nav {
    margin-bottom: 0;
}

nav ul {
    list-style-type: none;
    padding: 0;
}

nav ul li {
    display: inline;
    margin-right: 20px;
}

a {
    color: black;
    text-decoration: none;
}

.main-content {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.content-wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 20px;
}

/* Main Article: Left Section (Only 1 article) */
.main-article {
    border: 1px solid #ccc;
    padding: 20px;
    background-color: #f9f9f9;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.main-article img {
    width: 800px;
    height: auto;
    max-width: 300px; /* To ensure the image is not too large */
    margin-bottom: 15px;
}

.main-article h2 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    line-height: 1.5rem;
}

.main-article p {
    font-size: 1rem;
    margin-bottom: 10px;
}

.main-article a {
    font-size: 1rem;
    color: blue;
    text-decoration: underline;
    margin-top: 10px;
}

.category {
    text-align: left;
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 5px;
}

/* Other Articles: Center Section (Display in Rows) */
.other-articles {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.other-articles .article {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    padding: 10px;
}

.other-articles img {
    width: 200px;
    margin-right: 10px;
    height: auto;
}

.other-articles h2 {
    font-size: 10px;
    margin: 0;
    text-align: center;
    line-height: 1.5rem;
}

.other-articles p {
    font-size: 1rem;
    margin: 0;
    text-align: center;
    line-height: 1.5rem;
}

/* More Articles: Right Section (Links) */
.more-articles a {
    display: block;
    margin-bottom: 10px;
    color: blue;
}

.suggestions-list {
    border: 1px solid #ccc;
    background-color: white;
    max-height: 150px;
    overflow-y: auto;
    position: absolute;
    width: 100%;
}

.suggestion-item {
    padding: 5px;
    cursor: pointer;
}

.suggestion-item:hover {
    background-color: #000000;
}

/* Responsive Adjustments for Mobile */
@media (max-width: 768px) {
    .content-wrapper {
        grid-template-columns: 1fr;
    }

    .main-content {
        flex-direction: column;
    }
}
